.App {
  text-align: center;
}
*{
  box-sizing: border-box;
}
button:focus{
  outline: none;
}
header{
  background-color: #000;
  display:flex;
  justify-content:center;
  align-items: center;
  height: 60px;
  color: #fff;
  padding: 0 20px;
}
header h1{
  font-weight: normal;
  font-size: 22px;
}
.calculator{
  width:100%;
  display: flex;
  justify-content: center;
}
.inner{
  width:100%;
  max-width: 200px;
  margin:40px auto;
}
p.top{
  border: 1px solid #ddd;
  margin-bottom:0;
  padding: 10px;
  border-bottom: none;
  background: #fefefe;
}
p.bottom{
  border: 1px solid #ddd;
  margin-top:0;
  background: #fefefe;
  border-top: none;
  padding:10px;
}
.bottom button{
  height: 30px;
  background: #000;
  border:none;
  border-radius: 3px;
  cursor: pointer;
  color: #fff;
}
.numbers{
  display:flex;
  width: 100%;
  justify-content: space-evenly;
  flex-wrap: wrap;
  border:1px solid #ddd;
  background: #fefefe;
}
.numbers button{
  width:40px;
  height: 40px;
  border-radius: 3px;
  font-size: 14px;
  color: #fff;
  text-align: center;
  background-color:#46b5f8;
  border:none;
  margin: 10px;
  cursor: pointer;
}
